import trackNode from '../../components/trackNode.vue'
export default {
	components: {
		trackNode
	},
	data() {
		return {
			ShipperCode: '',
			LogisticCode: '',
			tracesData: [],
		}
	},
	onLoad(option) {
		this.getlogistic(option)
	},
	onShow() {

	},
	methods: {
		getlogistic(option) {
			console.log(option);
			let data = {
				id: option.id
			};
			this.$xm.post('/Index/getordership', data, (res) => {
				this.LogisticCode = res.LogisticCode;
				this.ShipperCode = res.order_ship;
				res.Traces.forEach(ele => {
					ele.status = res.State;
					switch (ele.status) {
						case '0':
							ele.statusName = '暂无物流信息'
							ele.isMainNode = false;
							break;
						case '1':
							ele.statusName = '已发货'
							ele.isMainNode = true;
							break;
						case '2':
							ele.statusName = '运输中'
							ele.isMainNode = true;
							break;
						case '3':
							ele.statusName = '已签收'
							ele.isMainNode = true;
							break;
						case '4':
							ele.statusName = '问题件'
							ele.isMainNode = false;
							break;
						default:
					}
				})
				this.tracesData = res.Traces.reverse();
				console.log(this.tracesData)
			})
		},
	}
}
